  import uniPopup from '@/components/uni-popup/uni-popup.vue'
  import uniIcon from "@/components/uni-icon/uni-icon.vue";
	export default {
		data() {
			return {
        type: '',
        show:false
			};
		},
    methods:{
      togglePopup(){
        this.type = "bottom";
        this.$refs['popup'].open();
      },  
      changeText(e){
       if(e.detail.value.trim().length !== 0){
         this.show = true;
       }else{
         this.show = false;
       }
      }
    },
    components:{
      uniIcon,
      uniPopup
    }
	}
